'use client';
import { useState } from 'react';

interface Country {
  id: string;
  name: string;
  military_strength: number;
  nuclear_arsenal: number;
  technology_level: number;
}

interface MilitaryStrategyProps {
  countries: Country[];
}

export default function MilitaryStrategy({ countries }: MilitaryStrategyProps) {
  const [selectedCountry, setSelectedCountry] = useState('');
  const [actionType, setActionType] = useState('military_exercises');
  const [intensity, setIntensity] = useState(50);
  const [result, setResult] = useState('');

  const targetCountries = countries.filter(country => country.name !== 'SZABAN');

  const executeMilitaryAction = async () => {
    if (!selectedCountry) return;

    try {
      const response = await fetch('http://localhost:8000/military/action', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          target_country: selectedCountry,
          action_type: actionType,
          intensity: intensity,
          details: {}
        })
      });

      const data = await response.json();
      setResult(data.result);
      
      if (data.consequences) {
        console.log('Consequences:', data.consequences);
      }
    } catch (error) {
      console.error('Error executing military action:', error);
      setResult('Failed to execute military action');
    }
  };

  return (
    <div className="military-strategy">
      <h2>‚öîÔ∏è Military Strategy Command</h2>
      
      <div className="strategy-grid">
        <div className="action-panel">
          <h3>Military Operations</h3>
          
          <div className="form-group">
            <label>Target Country:</label>
            <select value={selectedCountry} onChange={(e) => setSelectedCountry(e.target.value)}>
              <option value="">Select target</option>
              {targetCountries.map(country => (
                <option key={country.id} value={country.name}>
                  {country.name} (Military: {country.military_strength})
                </option>
              ))}
            </select>
          </div>

          <div className="form-group">
            <label>Operation Type:</label>
            <select value={actionType} onChange={(e) => setActionType(e.target.value)}>
              <option value="military_exercises">Military Exercises</option>
              <option value="nuclear_threat">Nuclear Deterrence</option>
              <option value="covert_ops">Covert Operations</option>
              <option value="cyber_attack">Cyber Attack</option>
            </select>
          </div>

          <div className="form-group">
            <label>Intensity: {intensity}%</label>
            <input 
              type="range" 
              min="10" 
              max="100" 
              value={intensity}
              onChange={(e) => setIntensity(Number(e.target.value))}
            />
          </div>

          <button 
            className="btn-military"
            onClick={executeMilitaryAction}
            disabled={!selectedCountry}
          >
            Execute Military Operation
          </button>
        </div>

        <div className="intel-panel">
          <h3>Military Intelligence</h3>
          <div className="country-military-data">
            {targetCountries.map(country => (
              <div key={country.id} className="military-card">
                <h4>{country.name}</h4>
                <p>Military Strength: {country.military_strength}</p>
                <p>Nuclear Arsenal: {country.nuclear_arsenal}</p>
                <p>Tech Level: {country.technology_level}</p>
              </div>
            ))}
          </div>
        </div>

        {result && (
          <div className="result-panel">
            <h3>Operation Result</h3>
            <div className="operation-result">
              <p>{result}</p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}