'use client';
import { useState } from 'react';

interface Country {
  id: string;
  name: string;
  public_opinion: number;
  alliances: string[];
  stability: number;
}

interface DiplomaticRelationsProps {
  countries: Country[];
}

export default function DiplomaticRelations({ countries }: DiplomaticRelationsProps) {
  const [selectedCountry, setSelectedCountry] = useState('');
  const [diplomacyType, setDiplomacyType] = useState('alliance');
  const [terms, setTerms] = useState('');
  const [result, setResult] = useState('');

  const targetCountries = countries.filter(country => country.name !== 'SZABAN');

  const executeDiplomacy = async () => {
    if (!selectedCountry) return;

    try {
      const response = await fetch('http://localhost:8000/diplomacy/action', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          target_country: selectedCountry,
          diplomacy_type: diplomacyType,
          terms: terms ? JSON.parse(terms) : {}
        })
      });

      const data = await response.json();
      setResult(data.result);
    } catch (error) {
      console.error('Error executing diplomacy:', error);
      setResult('Failed to execute diplomatic action');
    }
  };

  return (
    <div className="diplomatic-relations">
      <h2>ü§ù Diplomatic Relations</h2>
      
      <div className="diplomacy-grid">
        <div className="action-panel">
          <h3>Diplomatic Actions</h3>
          
          <div className="form-group">
            <label>Target Country:</label>
            <select value={selectedCountry} onChange={(e) => setSelectedCountry(e.target.value)}>
              <option value="">Select country</option>
              {targetCountries.map(country => (
                <option key={country.id} value={country.name}>
                  {country.name} (Relations: {country.public_opinion})
                </option>
              ))}
            </select>
          </div>

          <div className="form-group">
            <label>Diplomacy Type:</label>
            <select value={diplomacyType} onChange={(e) => setDiplomacyType(e.target.value)}>
              <option value="alliance">Form Alliance</option>
              <option value="treaty">Sign Treaty</option>
              <option value="negotiation">Initiate Negotiation</option>
              <option value="summit">Host Summit</option>
            </select>
          </div>

          <div className="form-group">
            <label>Terms (JSON):</label>
            <textarea 
              value={terms}
              onChange={(e) => setTerms(e.target.value)}
              placeholder='{"treaty_type": "trade", "szaban_position": "..."}'
              rows={3}
            />
          </div>

          <button 
            className="btn-diplomacy"
            onClick={executeDiplomacy}
            disabled={!selectedCountry}
          >
            Execute Diplomatic Action
          </button>
        </div>

        <div className="relations-panel">
          <h3>Current Relations</h3>
          <div className="relations-list">
            {targetCountries.map(country => (
              <div key={country.id} className="relation-card">
                <h4>{country.name}</h4>
                <p>Relations: {country.public_opinion}/100</p>
                <p>Stability: {country.stability}/100</p>
                <p>Alliances: {country.alliances.length}</p>
                {country.alliances.includes('SZABAN') && (
                  <span className="allied-badge">ALLIED</span>
                )}
              </div>
            ))}
          </div>
        </div>

        {result && (
          <div className="result-panel">
            <h3>Diplomatic Result</h3>
            <div className="diplomacy-result">
              <p>{result}</p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}